:root {
  --innerValue: 1560;
  --inner: 1560px;
  --padding-pc: 40px;
  --padding-sp: 35px;
  --base-color: #3b3b3b;
  --white: #ffffff;
  --black: #000000;
  --yellow: #fef30b;
  --yellow-light: #fff982;
  --pink: #efb9b9;
  --green: #0ba500;
  --green-light: #b1f19e;
  --gray: #707070;
  --light: 300;
  --normal: 400;
  --medium: 500;
  --semi-bold: 600;
  --bold: 700;
  --ex-bold: 800;
  --sans-family: "Noto Sans JP", sans-serif;
  --duration: 0.3s ease;
  --header-height-pc: 118px;
  --header-height-sp: 60px;
}

.hoverOpacity {
  transition: opacity var(--duration);
}
@media (any-hover: hover) {
  .hoverOpacity:hover {
    opacity: 0.7;
  }
}

@media screen and (max-width:834px) {
  body:has(#mobile-menu-button[aria-expanded=true]) {
    overflow: hidden;
  }
}

header {
  height: var(--header-height-pc);
}
header * {
  box-sizing: border-box;
}
@media screen and (max-width:834px) {
  header {
    height: var(--header-height-sp);
  }
}
header .header-content {
  height: inherit;
}

.header-left {
  height: inherit;
}
.header-left a {
  display: block;
  height: inherit;
}
.header-left img {
  height: inherit;
}

@media screen and (max-width:834px) {
  .header-right {
    display: flex;
    gap: 10px;
    align-items: center;
  }
}
.header-right .header-shop-menu img {
  width: clamp(150px, calc(90px + 15.3846153846vw), 330px);
}
@media screen and (max-width:834px) {
  .header-right .main-navigation {
    display: none;
  }
}
@media screen and (max-width:834px) {
  .header-right .mobile-menu-toggle {
    display: block;
    height: 100%;
  }
}
.header-right .mobile-menu-toggle button {
  display: block;
  padding: 0;
  border: none;
  background: none;
  width: 40px;
  aspect-ratio: 1/1;
  position: relative;
}
.header-right .mobile-menu-toggle button span {
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transform-origin: right center;
  transition: all var(--duration);
}
.header-right .mobile-menu-toggle button span:nth-child(1) {
  top: 20%;
}
.header-right .mobile-menu-toggle button span:nth-child(2) {
  top: 45%;
}
.header-right .mobile-menu-toggle button span:nth-child(3) {
  bottom: 0;
}
.header-right .mobile-menu-toggle button[aria-expanded=true] span:nth-child(1) {
  top: 0%;
  transform: translateX(-60%) rotate(-45deg);
}
.header-right .mobile-menu-toggle button[aria-expanded=true] span:nth-child(2) {
  top: 55%;
  transform: translateX(-60%) rotate(45deg);
}
.header-right .mobile-menu-toggle .menu-toggle-icon {
  display: block;
  width: 80%;
  height: 3px;
}
.header-right .mobile-menu-toggle .menu-toggle-text {
  font-size: 10px;
  margin-left: initial;
  width: 100%;
}

.drawer-menu {
  display: block;
  position: fixed;
  top: var(--header-height-sp);
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: calc(100% - var(--header-height-sp));
  overflow-y: scroll;
  z-index: 100;
  background: #efefef;
  pointer-events: none;
  clip-path: inset(0 0 100% 0);
  transition: opacity var(--duration), visibility var(--duration), clip-path var(--duration);
}
@media screen and (min-width:835px) {
  .drawer-menu {
    display: none;
  }
}

.drawer-menu[aria-hidden=false],
.drawer-menu:popover-open {
  pointer-events: auto;
  clip-path: inset(0 0 0 0);
}

.drawer-menu__lists {
  display: block;
  width: 100%;
  margin-inline: auto;
  padding-block: 30px 80px;
  padding-inline: var(--padding-sp);
}

.drawer-menu__list {
  border-bottom: 1px solid var(--base-color);
}
.drawer-menu__list a {
  display: block;
  width: 100%;
  height: 100%;
  padding: 20px 10px;
  text-align: center;
  font-size: 16px;
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
  text-align: left;
  color: var(--base-color);
}
.drawer-menu__list a span {
  display: block;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(0%, -50%) rotate(90deg);
  transition: transform var(--duration);
  color: var(--base-color);
}
.drawer-menu__list a span::before, .drawer-menu__list a span::after {
  content: "";
  display: block;
  width: inherit;
  height: 2px;
  background: currentColor;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  right: 0;
  transform-origin: center right;
}
.drawer-menu__list a span::before {
  transform: translate(0%, -50%) rotate(45deg);
}
.drawer-menu__list a span::after {
  transform: translate(0%, -50%) rotate(-45deg);
}
.drawer-menu__list.is_active a span {
  transform: translate(0%, -50%) rotate(90deg) scale(-1, 1);
}

.footer {
  box-sizing: border-box;
  font-family: var(--sans-family);
  text-align: left;
  padding-block-end: clamp(38px, calc(-1.3333333333px + 10.0854700855vw), 156px);
  margin-block-start: clamp(85px, calc(25px + 15.3846153846vw), 265px);
}
@media screen and (max-width:834px) {
  .footer {
    background-color: color-mix(in srgb, var(--gray) 12%, transparent);
  }
}
.footer * {
  box-sizing: border-box;
}

.footer__inner {
  max-width: calc(var(--inner) + var(--padding-pc) * 2);
  margin-inline: auto;
  padding-inline: var(--padding-pc);
}
@media screen and (max-width:834px) {
  .footer__inner {
    padding-inline: var(--padding-sp);
  }
}

.footer__contents {
  border-top: 1px solid var(--gray);
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  padding-block-start: clamp(22px, calc(-10.6666666667px + 8.3760683761vw), 120px);
}
@media screen and (max-width:834px) {
  .footer__contents {
    border-top: none;
    display: block;
  }
}

.footer__main {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  column-gap: 20px;
  width: max(600px, 62%);
  padding-inline: min(130px, 6vw) 50px;
}
@media screen and (max-width:834px) {
  .footer__main {
    flex-direction: column;
    width: 100%;
  }
}

@media screen and (max-width:834px) {
  .footer__mainWrap {
    display: contents;
  }
}

@media screen and (max-width:834px) {
  .footer__head {
    order: 1;
    text-align: center;
  }
}

.footer__logo {
  width: clamp(108px, calc(46px + 15.8974358974vw), 294px);
}
@media screen and (max-width:834px) {
  .footer__logo {
    display: inline-block;
  }
}
.footer__logo img {
  width: 100%;
  height: auto;
}

.footer__body {
  margin-block-start: 9px;
}
@media screen and (max-width:834px) {
  .footer__body {
    margin-block-start: 30px;
    order: 3;
  }
}

.footer__snsTitle {
  font-size: 20px;
  font-family: var(--sans-family);
  font-weight: var(--ex-bold);
  line-height: 1.45;
  letter-spacing: 0.067em;
}
@media screen and (max-width:834px) {
  .footer__snsTitle {
    padding-block-end: 10px;
    border-bottom: 1px solid var(--gray);
    text-align: center;
  }
}

.footer__snsLists {
  margin-block-start: 14px;
}
@media screen and (max-width:834px) {
  .footer__snsLists {
    margin-block-start: 30px;
  }
}

@media screen and (max-width:834px) {
  .footer__list {
    text-align: center;
  }
}

.footer__list.mt {
  margin-block-start: 50px;
}

.footer__snsList + .footer__snsList {
  margin-block-start: 10px;
}
@media screen and (max-width:834px) {
  .footer__snsList + .footer__snsList {
    margin-block-start: 20px;
  }
}

.footer__snsText {
  font-size: clamp(12px, calc(9.3333333333px + 0.6837606838vw), 20px);
}
@media screen and (max-width:834px) {
  .footer__snsText {
    text-align: center;
  }
}

.footer__snsLink {
  display: block;
  width: 45px;
  margin-block-start: 10px;
  text-align: center;
}
@media screen and (max-width:834px) {
  .footer__snsLink {
    margin-inline: auto;
  }
}

@media screen and (max-width:834px) {
  .footer__bottom {
    order: 2;
  }
}

.footer__adBlock + .footer__adBlock {
  margin-block-start: 15px;
}

.footer__adText {
  font-size: clamp(12px, calc(9.3333333333px + 0.6837606838vw), 20px);
  line-height: 1.45;
}
@media screen and (max-width:834px) {
  .footer__adText {
    text-align: center;
  }
}

.footer__adTel {
  font-size: clamp(20px, calc(14.3333333333px + 1.452991453vw), 37px);
  line-height: 1.4594594595;
}
@media screen and (max-width:834px) {
  .footer__adTel {
    text-align: center;
  }
}

.footer__sub {
  width: max(400px, 38%);
  padding-inline: 50px;
  border-right: 1px solid var(--gray);
}
@media screen and (max-width:834px) {
  .footer__sub {
    display: none;
  }
}

.footer__listWrap {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 60px;
}

.footer__list {
  margin-block-start: 10px;
}
.footer__list a {
  font-size: 20px;
  line-height: 1.45;
  letter-spacing: 0.067em;
  color: var(--base-color);
  text-decoration: none;
}

.footer__subLists {
  padding-left: 2em;
}

.footer__subList a {
  font-size: 20px;
  line-height: 1.45;
  letter-spacing: 0.067em;
  color: var(--base-color);
  text-decoration: none;
}

.footer__copy {
  font-size: clamp(12px, calc(9.3333333333px + 0.6837606838vw), 20px);
  line-height: 1.45;
  text-align: center;
  margin-block-start: clamp(30px, calc(-49.6666666667px + 20.4273504274vw), 269px);
}

.sub {
  color: var(--base-color);
}
.sub .inner {
  max-width: calc(var(--inner) + var(--padding-pc) * 2);
  margin-inline: auto;
  padding-inline: var(--padding-pc);
}
@media screen and (max-width:834px) {
  .sub .inner {
    padding-inline: var(--padding-sp);
  }
}
.sub .secTitle {
  font-size: clamp(19px, calc(0.3333333333px + 4.7863247863vw), 75px);
  line-height: 1.4266666667;
}
.sub .secTitleSub {
  font-size: clamp(16px, calc(4.6666666667px + 2.905982906vw), 50px);
  line-height: 1.44;
}
.sub .baseText {
  font-size: clamp(14px, calc(11px + 0.7692307692vw), 23px);
  line-height: 1.4347826087;
}
.sub .subInfo {
  background: var(--white);
  margin-block-start: clamp(147px, calc(-30.6666666667px + 45.5555555556vw), 680px);
}
.sub .relatedInfo__body {
  margin-block-start: clamp(33px, calc(-45.3333333333px + 20.0854700855vw), 268px);
}
.sub .relatedInfo__items {
  max-width: 812px;
  margin-inline: auto;
}
.sub .relatedInfo__item {
  display: block;
  width: 100%;
  position: relative;
}
.sub .relatedInfo__item + .relatedInfo__item {
  margin-block-start: clamp(45px, calc(32.3333333333px + 3.2478632479vw), 83px);
}
.sub .relatedInfo__img {
  aspect-ratio: 812/247;
}
.sub .relatedInfo__img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.sub .relatedInfo__content {
  position: absolute;
  right: 15px;
  z-index: 2;
}
.sub .relatedInfo__content.positionTop {
  top: clamp(7px, calc(1px + 1.5384615385vw), 25px);
}
.sub .relatedInfo__content.positionBottom {
  bottom: clamp(7px, calc(1px + 1.5384615385vw), 25px);
}
.sub .relatedInfo__title {
  font-size: clamp(12px, calc(5.3333333333px + 1.7094017094vw), 32px);
  font-weight: var(--bold);
  letter-spacing: 0.067em;
  line-height: 1.4375;
}
.sub .relatedInfo__title.white {
  color: var(--white);
}
.sub .relatedInfo__title.gray {
  color: var(--gray);
}
.sub .relatedInfo__deco {
  font-size: clamp(10px, calc(7.3333333333px + 0.6837606838vw), 18px);
  line-height: 1.4444444444;
  font-weight: var(--medium);
  margin-block-start: clamp(7px, calc(2.6666666667px + 1.1111111111vw), 20px);
  margin-inline-start: auto;
  background: var(--gray);
  color: var(--white);
  width: clamp(58px, calc(28.3333333333px + 7.6068376068vw), 147px);
  min-height: clamp(20px, calc(18px + 0.5128205128vw), 26px);
  text-transform: uppercase;
  display: grid;
  place-content: center;
}